@import "./variables.module.scss";
//header
.carChargeStatus{
    height: 30px;
    display:flex;
    justify-content: space-between;
    a{
        text-decoration: none;
        color:black;
    }
    button{
        width: 10%;
        background-color: white;
        border:none;
    }
    .carChargeStatus__home{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 20%;
    }
    .carChargeStatus__stautus{
        display:flex;
        align-items: center;
        justify-content: center;
        width: 80%;
    } 
}

//main
.carChargeSection{
    background-color: $background-color;
    width: 90%;height: 330px;
    margin:0 auto;
    margin-top:40px;

    border-radius: 10%;
    .carChargeSection__img{
        width: 100%;
    }
    .carChargeSection__box{
        
        .carChargeSection__text{
            text-align: center;
            p{
                opacity: 0.5;
                line-height: 25px;
                margin-left:8px;
            }
        }
    }
    .text{
        width:120px; height: 30px;
        background-color: $main-color;
        margin:0 auto;
        margin-top:5px;
        line-height: 30px;
        text-align: center;
        border-radius: 10px;
        .text1{
            color:white;
        }
    }
}

//footer
footer{
    width: 100%; height: 60px;
    background-color: red;;
    position: absolute;
    bottom:0;
    div{
        width: 100%;
        display:flex;
        button{
           width: 100%; height: 60px;
            background-color: white;
            border:none;
            color:white;
            background-color: $button-color;
            font-size:17px;
            font-weight: bold;
        }
    }
}